
.contact[data-v-6ad140cc]{
  position: relative;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}
.title[data-v-6ad140cc]{
    padding-left: 16px;
    height: 34px;
    line-height: 34px;
    font-weight: 700;
    background-color: #5264AE;
    color: white;
}
.content[data-v-6ad140cc]{
    margin: 6px 10px;
    margin-top: 28px;
}
.group[data-v-6ad140cc] { 
  position:relative; 
  margin-top:24px;
}
.group.inputText[data-v-6ad140cc] { 
  position:relative; 
  margin-top:45px;
}
input[data-v-6ad140cc] 				{
  font-size:24px;
  display:block;
  width:100%;
  border:none;
  border-bottom:1px solid #e9e9eb;
  font-weight: 100;
  font-size: 20px;
}
input[data-v-6ad140cc]:focus 		{ outline:none;
}

/* LABEL ======================================= */
label[data-v-6ad140cc] 				 {
  color:#999; 
  font-size:18px;
  font-weight:normal;
  position:absolute;
  pointer-events:none;
  left:5px;
  top:10px;
  transition:0.2s ease all; 
  -moz-transition:0.2s ease all; 
  -webkit-transition:0.2s ease all;
}

/* active state */
input:focus ~ label[data-v-6ad140cc], input:valid ~ label[data-v-6ad140cc] 		{
  top:-24px;
  font-size:18px;
  color:gray;
}

/* BOTTOM BARS ================================= */
.bar[data-v-6ad140cc] 	{ position:relative; display:block; width:100%;
}
.bar[data-v-6ad140cc]:before, .bar[data-v-6ad140cc]:after 	{
  content:'';
  height:3px; 
  width:0;
  bottom:1px; 
  position:absolute; 
  transition:0.2s ease all; 
  -moz-transition:0.2s ease all; 
  -webkit-transition:0.2s ease all;
}
.bar[data-v-6ad140cc]:before {
  left:50%;
}
.bar[data-v-6ad140cc]:after {
  right:50%;
}

/* active state */
input:focus ~ .bar[data-v-6ad140cc]:before, input:focus ~ .bar[data-v-6ad140cc]:after,
.group.select input ~ .bar[data-v-6ad140cc]:before, .group.select input ~ .bar[data-v-6ad140cc]:after{
  width:50%;
}

/* HIGHLIGHTER ================================== */
.highlight[data-v-6ad140cc] {
  position:absolute;
  height:60%; 
  width:100px; 
  top:25%; 
  left:0;
  pointer-events:none;
  opacity:0.5;
}

/* active state */
input:focus ~ .highlight[data-v-6ad140cc] {
  -webkit-animation:inputHighlighter-data-v-6ad140cc 0.3s ease;
  animation:inputHighlighter-data-v-6ad140cc 0.3s ease;
}
.group .btn[data-v-6ad140cc]{
    width: 100%;
    padding: 0px 0px;
    height: 48px;
    color: #fff;
    border: 0 none;
    font-size: 22px;
    font-weight: 500;
    line-height: 34px;
    color: #202129;
    background-color: #edeeee;
}
.group.select .btn[data-v-6ad140cc]{
    /* border: 6px solid #C0C0C0; */
    line-height: 18px;
}
.group .btn.btn-green[data-v-6ad140cc]{
  border: 1px solid #0b81ff;
  color: #0b81ff;
  background-color: white;
  font-weight: 500;
  border-radius: 28px;
}
.group.select .btn.btn-green[data-v-6ad140cc], .group:hover .btn.btn-green[data-v-6ad140cc]{
  background-image: -webkit-gradient(linear, left top, right top, from(#62A3FF), color-stop(#4994FF) , to(#0b81ff));
  background-image: linear-gradient(to right, #62A3FF, #4994FF , #0b81ff); 
  color: white;
  border: none;
}
.group .btn.btn-orange[data-v-6ad140cc]{
  border: 1px solid #B6B6B6;
  color: #B6B6B6;
  background-color: white;
  font-weight: 500;
  border-radius: 28px;
}
.group.select .btn.btn-orange[data-v-6ad140cc], .group:hover .btn.btn-orange[data-v-6ad140cc]{

  background-image: -webkit-gradient(linear, left top, right top, from(#D3D3D3), color-stop(#C5C5C5) , to(#B6B6B6));

  background-image: linear-gradient(to right, #D3D3D3, #C5C5C5 , #B6B6B6); 
  color: white;
  border: #B6B6B6;
}
.group .btn.btn-red[data-v-6ad140cc]{
  border: 1px solid #e74c3c80;
  color: #e74c3c;
  background-color: white;
  font-weight: 500;
  border-radius: 28px;
}
.group.select .btn.btn-red[data-v-6ad140cc], .group:hover .btn.btn-red[data-v-6ad140cc]{
  background-image: -webkit-gradient(linear, left top, right top, from(#FF5B5B), color-stop(#FF4B4B) , to(#FE3C3C));
  background-image: linear-gradient(to right, #FF5B5B, #FF4B4B , #FE3C3C); 
  color: white;
  border: none;
}

/* ANIMATIONS ================ */
@-webkit-keyframes inputHighlighter-data-v-6ad140cc {
from { background:#5264AE;
}
to 	{ width:0; background:transparent;
}
}
@keyframes inputHighlighter-data-v-6ad140cc {
from { background:#5264AE;
}
to 	{ width:0; background:transparent;
}
}



